/* eslint-disable no-undef */
import { pipeline, env } from "@xenova/transformers";

env.allowLocalModels = false;
env.useBrowserCache = false;

class Pipeline {
  static sentimentTask = "sentiment-analysis";
  static sentimentModel = "Xenova/bert-base-multilingual-uncased-sentiment";
  static sentimentInstance = null;

  static summarizationTask = 'summarization';
  static summarizationModel = 'Xenova/bart-large-cnn';
  static summarizationInstance = null;

  static async getSentimentInstance(progress_callback = null) {
    if (this.sentimentInstance === null) {
      this.sentimentInstance = await pipeline(this.sentimentTask, this.sentimentModel, { progress_callback });
    }
    return this.sentimentInstance;
  }

  static async getSummarizationInstance(progress_callback = null) {
    if (this.summarizationInstance === null) {
      this.summarizationInstance = await pipeline(this.summarizationTask, this.summarizationModel, { progress_callback});
    }
    return this.summarizationInstance;
  }
}

globalThis.addEventListener("message", async (event) => {
  if (event.data.task.toLowerCase().includes('sentiment')) {
    const sentimentAnalyser = await Pipeline.getSentimentInstance((e) => {
      globalThis.postMessage(e)
    });
    const sentiment = await sentimentAnalyser(event.data.text);
    globalThis.postMessage({
      status: "sentiment-complete",
      sentiment: sentiment,
    });
  } else if (event.data.task.toLowerCase().includes('summarization')) {
    const summarizationProcess = await Pipeline.getSummarizationInstance((e) => {
      globalThis.postMessage(e)
    });
    const summarization = await summarizationProcess(event.data.text);
    globalThis.postMessage({
      status: 'summarization-complete',
      summarization: summarization
    });
  }
});
